<template lang="pug">
  .date-pannel
    .date
      .date__text {{ currentTime }}
</template>

<script>
import {
  TIME_FORMAT,
  TIMEZONE_ENABLE,
  TIMEZONE_LIST
} from "../../configs/site.js";
import moment from "moment-timezone";

export default {
  name: "date-pannel",

  data() {
    return {
      currentTime: "",
      TIMEZONE_ENABLE,
      TIMEZONE_LIST
    };
  },

  created() {
    this.currentTime = moment().format(TIME_FORMAT + " (ZZ)");
  }
};
</script>
