        p.aside-menu__version 2020/04/20 v2
</template>

<script>
import { MENU_LIST } from "@/configs/site.js";

export default {
  props: {
    menuEnable: Boolean
  },

  data() {
    return {
      MENU_LIST
    };
  },

  watch: {
    'menuEnable': function () {
      if (this.menuEnable) {
        this.activeLi && this.$refs.menu.open(this.activeLi.toString())
      } else {
        if (this.activeLi) {
          this.$refs.menu.close(this.activeLi.toString())
        } else {
          let num = this.$refs.menu.$children.length
          for (let i = 0; i < num; i++) {
            this.$refs.menu.close(i.toString())
          }
        }
      }
    },
  },

  methods: {
    findMenuActive (index) {
      this.activeLi = index.toString()
      this.menuEnable || (this.$emit('toggleMenu'))
    }
  }
};
</script>

